form {
  label {
    color: #4f5359;
    display: block;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    .required-asterisk {
      color: #f75c0a;
    }
  }
  input {
    &[type="text"],
    &[type="password"],
    &[type="email"] {
      display: block;
      width: 100%;
      font-size: 18px;
      line-height: 28px;
      border-width: 0 0 1px 0;
      border-style: solid;
      border-color: #afbdcb;
      border-radius: 0;
      padding-right: 0;
      padding-left: 0;
      height: 40px;
      font-weight: 400;
      width: 100%;
      margin-bottom: 3rem;
    }
    &::placeholder {
      color: #9ca4ae;
      font-weight: 400;
    }
    &[type="submit"] {
      width: 100%;
      margin-top: 3rem;
      margin-bottom: 3rem;
    }
    &[type="checkbox"] {
      margin-right: 5px;
      // all: unset;
      // width: 20px;
      // height: 20px;
      // border: 1px solid #afbdcb;
      // box-sizing: border-box;
      // position: relative;
      // top: 5px;
    }
  }
}

